<template>
  <div class="portrait">
    <div class="left">
      <div class="content-top">
        <div class="content-title">
          <span class="text">客户基本信息</span>
        </div>
        <div class="content-main content-boder">
          <div class="clientBaseData">
            <span class="content-item">
              <span class="label">目标对象编号：</span>
              <span class="value">{{ clientBaseData.objectNumber }}</span>
            </span>
            <span class="content-item">
              <span class="label">目标对象名称：</span>
              <span class="value">{{ clientBaseData.objectName }}</span>
            </span>
            <span class="content-item">
              <span class="label">用电地址：</span>
              <span class="value">{{ clientBaseData.address }}</span>
            </span>
            <span class="content-item">
              <span class="label">用户类别：</span>
              <span class="value">{{ clientBaseData.userCategory }}</span>
            </span>
            <span class="content-item">
              <span class="label">用电类别：</span>
              <span class="value">{{ clientBaseData.powerCategory }}</span>
            </span>
            <span class="content-item">
              <span class="label">关系人：</span>
              <span class="value">{{ clientBaseData.person }}</span>
            </span>
            <span class="content-item">
              <span class="label">联系电话：</span>
              <span class="value">{{ clientBaseData.phoneNumber }}</span>
            </span>
            <span class="content-item">
              <span class="label">线路名称：</span>
              <span class="value">{{ clientBaseData.lineName }}</span>
            </span>
            <span class="content-item">
              <span class="label">台区名称：</span>
              <span class="value">{{ clientBaseData.district }}</span>
            </span>
            <span class="content-item">
              <span class="label">是否现货客户：</span>
              <span class="value">{{ clientBaseData.customersSpot }}</span>
            </span>
            <span class="content-item">
              <span class="label">是否重点关注客户：</span>
              <span class="value">{{ clientBaseData.focusCustomer }}</span>
            </span>
          </div>
        </div>
      </div>
      <div class="content-bottom">
        <div class="content-title">
          <span class="text">策略推荐</span>
        </div>
        <div class="content-main content-boder">
          <bdaTable v-if="recommendRes" :tableData="recommendData" :headerColor="headerColor"></bdaTable>
        </div>
      </div>
    </div>
    <div class="right">
      <div class="content-top">
        <div class="search">
          <el-select
            v-model="clinetInfo"
            class="search-box"
            size="mini"
            filterable
            placeholder="请选择"
          >
            <el-option
              v-for="item in clinetList"
              :key="item.name"
              :label="item.name"
              :value="item.value"
            ></el-option>
          </el-select>
          <el-button type="primary" size="mini">搜索</el-button>
        </div>
        <div class="clinet-portrait">
          <div class="content-title">
            <span class="text">客户全景</span>
          </div>
          <div class="content-main content-boder">
            <portraitShow v-if="false"></portraitShow>
          </div>
        </div>
      </div>
      <div class="content-bottom">
        <div class="content-title">
          <span class="text">工单轨迹</span>
        </div>
        <div class="content-main content-boder">
          <!--  -->
          <div class="content-main-item" v-for="(item, index) in showNumberList" :key="index">
            <div class="item">
              <div class="item-top">
                <div class="line">
                  <div class="circle"></div>
                  <i
                    v-if="index === 0"
                    class="prev circle el-icon-arrow-left"
                    :class="startIndex === 0 ? 'stop' : ''"
                    @click="prevHistory"
                  ></i>
                  <i
                    v-if="index === showNumberList.length - 1"
                    class="next circle el-icon-arrow-right"
                    :class="endIndex === workOrderList.length ? 'stop' : ''"
                    @click="nextHistory"
                  ></i>
                </div>
                <div class="time">
                  <span>{{ item.time }}</span>
                </div>
              </div>
              <div class="item-bottom">
                <span>{{ item.clientNumber }}</span>
                <span>{{ item.text }}</span>
                <span>{{ item.region }}</span>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
// import Interface from '../base/mixin/talk-manage';
import bdaTable from '../../components/bda-table/bda-table'
import portraitShow from '../../components/portrait-show/portrait-show'

export default {
  components: {
    bdaTable,
    portraitShow
  },
  name: 'portrait',
  //   mixins: [Interface],
  data() {
    return {
      //客户基本信息
      clientBaseData: {},
      // 策略推荐
      recommendData: {},
      recommendRes: false,
      //客户查询
      clinetInfo: '',
      clinetList: [],
      //工单轨迹数据
      workOrderList: [],
      //工单轨迹数据 - 控制展示个数
      showNumberList: [1, 2, 3, 4, 5],
      startIndex: 0,
      endIndex: 5,
    }
  },
  mounted() {
    this.getClientDaseData()
    this.getRecommendData()
    this.getClinetList()
    this.getWorkOrderList()
  },
  methods: {
    //工单轨迹上一个
    nextHistory() {
      if (this.endIndex === this.workOrderList.length) return
      this.startIndex = this.startIndex + 1
      this.endIndex = this.endIndex + 1
      this.showNumberList = this.workOrderList.slice(this.startIndex, this.endIndex);

    },
    //工单轨迹下一个
    prevHistory() {
      if (this.startIndex === 0) return
      this.startIndex = this.startIndex - 1
      this.endIndex = this.endIndex - 1
      this.showNumberList = this.workOrderList.slice(this.startIndex, this.endIndex);
    },
    //获取客户基本信息
    getClientDaseData() {
      this.clientBaseData = {
        objectNumber: '030600000023211',
        objectName: '陈小明',
        address: '广州市黄埔区科学城科学大道162号创意大厦B3栋13楼1306',
        userCategory: '公变用户',
        powerCategory: '居民',
        person: '陈小明',
        phoneNumber: '13712345678 ',
        lineName: 'F180公线',
        district: 'F180公线',
        customersSpot: '是',
        focusCustomer: '是',
      }
    },
    // 获取策略推荐
    getRecommendData() {
      this.headerColor = '#1899fa'
      this.recommendRes = false
      this.$nextTick(() => {
        this.recommendData = {
          tableData: [
            {
              clientLabel: '经常投诉',
              clientNumber: '12345',
              seatAbility: '专家坐席',
              seatNumber: '坐席人数',
            },
            {
              clientLabel: '经常投诉',
              clientNumber: '12345',
              seatAbility: '专家坐席',
              seatNumber: '坐席人数',
            },
            {
              clientLabel: '经常投诉',
              clientNumber: '12345',
              seatAbility: '专家坐席',
              seatNumber: '坐席人数',
            },
            {
              clientLabel: '经常投诉',
              clientNumber: '12345',
              seatAbility: '专家坐席',
              seatNumber: '坐席人数',
            },
            {
              clientLabel: '经常投诉',
              clientNumber: '12345',
              seatAbility: '专家坐席',
              seatNumber: '坐席人数',
            },
            {
              clientLabel: '经常投诉',
              clientNumber: '12345',
              seatAbility: '专家坐席',
              seatNumber: '坐席人数',
            }],
          columnNameData: [
            { prop: "clientLabel", label: "客户标签" },
            { prop: "clientNumber", label: "标签覆盖客户数" },
            { prop: "seatAbility", label: "坐席能力" },
            { prop: "seatNumber", label: "坐席人数" },
          ]
        }
        this.recommendRes = true

      })
    },
    //获取客户信息列表
    getClinetList() {
      this.clinetList = [
        { name: '客户1', value: "00001" },
        { name: '客户2', value: "00002" },
        { name: '客户3', value: "00003" },
      ]
    },
    //获取工单轨迹数据
    getWorkOrderList() {
      this.workOrderList = [
        { time: "2020.6.3", clientNumber: '0300000023321233', text: '来电咨询电费', region: "佛山市南海区" },
        { time: "2020.6.8", clientNumber: '0300000023321233', text: '来电咨询电费', region: "佛山市南海区" },
        { time: "2020.6.17", clientNumber: '0300000023321233', text: '来电咨询电费', region: "佛山市南海区" },
        { time: "2020.7.2", clientNumber: '0300000023321233', text: '来电咨询电费', region: "佛山市南海区" },
        { time: "2020.7,5", clientNumber: '0300000023321233', text: '来电咨询电费', region: "佛山市南海区" },
        { time: "2020.7.7", clientNumber: '0300000023321233', text: '来电咨询电费', region: "佛山市南海区" },
      ]
      this.startIndex = 0
      this.endIndex = 5
      this.showNumberList = this.workOrderList.slice(this.startIndex, this.endIndex);
      console.log(this.showNumberList, 'this.showNumberList');
    }
  },
}
</script>

<style scoped lang="scss">
::v-deep .el-table .warning-row {
  background: #e9f3fe;
}
.portrait {
  width: 100%;
  height: 100%;
  font-size: 12px;
  box-sizing: border-box;
  display: flex;
  .content-title {
    width: 100%;
    height: 30px;
    line-height: 30px;
    font-size: 14px;
    font-weight: bold;
    color: #fff;
    background-image: linear-gradient(270deg, #17a8ff 0%, #1a84f4 100%);
    border-radius: 4px 4px 0 0;
    .text {
      margin-left: 10px;
    }
  }
  .content-main {
    height: calc(100% - 30px);
    padding: 10px;
    box-sizing: border-box;
  }
  .content-boder {
    background: #ffffff;
    border: 2px solid rgba(26, 132, 244, 0.3);
    box-shadow: inset 0 0 20px 0 rgba(26, 132, 244, 0.6);
    border-radius: 0 0 4px 4px;
    border-top: 0;
  }

  .left {
    width: 400px;
    height: 100%;
    margin-right: 20px;
    .content-top {
      height: 50%;
      margin-bottom: 20px;
      .content-main {
        .clientBaseData {
          height: 100%;
          .content-item {
            display: flex;
            align-items: center;
            min-height: 9%;
            .label {
              width: 30%;
              height: 100%;
              color: #808080;
              text-align: right;
            }
            .value {
              width: 70%;
              height: 100%;
              color: #1a84f4;
              font-weight: bold;
            }
          }
        }
      }
    }
    .content-bottom {
      height: calc(50% - 20px);
      .content-title {
        background-image: linear-gradient(270deg, #ebcb60 0%, #d2920f 100%);
      }
      .content-main {
        border-color: rgba(210, 147, 15, 0.3);
        box-shadow: inset 0 0 20px 0 rgba(210, 147, 15, 0.6);
      }
    }
  }
  .right {
    width: calc(100% - 400px - 20px);
    height: 100%;

    .content-top {
      width: 100%;
      height: calc(100% - 200px - 20px);
      margin-bottom: 20px;
      .search {
        margin-bottom: 20px;
        .search-box {
          width: calc(100% - 57px);
        }
      }
      .clinet-portrait {
        width: 100%;
        height: calc(100% - 50px);

        .content-title {
          background-image: linear-gradient(270deg, #38c7b1 0%, #11a5b0 100%);
        }
        .content-main {
          border-color: rgba(17, 165, 176, 0.3);
          box-shadow: inset 0 0 20px 0 rgba(17, 165, 176, 0.6);
        }
      }
    }
    .content-bottom {
      width: 100%;
      height: 200px;
      .content-title {
        background-image: linear-gradient(270deg, #60a1eb 0%, #2a75d3 100%);
      }
      .content-main {
        border-color: rgba(42, 118, 211, 0.3);
        box-shadow: inset 0 0 20px 0 rgba(17, 165, 176, 0.6);
        width: 100%;
        display: flex;
        .stop {
          cursor: not-allowed;
          color: #eee;
        }
        .content-main-item {
          // vertical-align: top;
          width: 20%;
          height: 100%;
          display: flex;
          margin-top: 10px;
          /*border: 1px solid #cccccc;*/
          .item {
            width: 100%;
            .item-top {
              .line {
                width: 100%;
                height: 0px;
                margin: 10px 0px;
                border: 2px solid #dae6f2;
                text-align: center;
                position: relative;

                .circle {
                  width: 12px;
                  height: 12px;
                  border-radius: 50%;
                  border: 2px solid #17bb9f;
                  background-color: #ffffff;
                  position: absolute;
                  top: -8px;
                  right: calc(50% - 5px);
                }

                .next {
                  width: 16px;
                  height: 16px;
                  top: -10px;
                  border: 2px solid #dae6f2;
                  right: 0;
                  &:before {
                    line-height: 17px;
                  }
                }
                .prev {
                  width: 16px;
                  height: 16px;
                  top: -10px;
                  border: 2px solid #dae6f2;
                  left: 0;
                  &:before {
                    line-height: 17px;
                  }
                }
              }
              .time {
                margin: 10px 0px;
                text-align: center;
              }
            }
            .item-bottom {
              width: 100%;
              height: calc(100% - 24px - 26px);
              span {
                display: block;
                text-align: center;
                color: #808080;
                height: 20px;
                line-height: 20px;
                font-weight: bold;
                &:nth-child(2) {
                  color: #1a84f4;
                }
              }
            }
          }
        }
      }
    }
  }
}
</style>
